<template>
	<div id="kefu-setting">
		<public-header title="顾客消息设置"></public-header>
		<div class="content">
			<van-cell-group class="margin-10-b status border-0px">
				<van-cell title="顾客即时消息" label="门店开启此设置，方便与顾客在线沟通交流" class="border-0px">
					<iswitch :value="store.kefu_status" condition-open="1" condition-close="0" slot="right-icon" text-open="ON" text-close="OFF"  @change="jsToggleSwitch" keys="store.kefu_status" type="kefu_status"></iswitch>
				</van-cell>
				<div class="tips">请保证及时回复顾客消息，长期不及时回复将影响您的商家信誉</div>
			</van-cell-group>
			<van-cell-group class="margin-10-t border-0px">
				<van-cell title="当前客服状态" label="此处设置为店员的客服状态" :value="clerk.kefu_status_cn" is-link :to="util.getUrl({path: '/pages/kefu/update', query: {type: 'kefu_status'}})" />
			</van-cell-group>
			<van-cell-group class="margin-10-t border-0px">
				<van-cell class="ellipsis" title="忙碌状态自动回复" :value="store.data.kefu.busy_reply ? store.data.kefu.busy_reply : '暂未设置'" is-link :to="util.getUrl({path: '/pages/kefu/update', query: {type: 'busy_reply'}})" />
			</van-cell-group>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import PublicHeader from '@/components/header'
	import iswitch from "@/components/switch"
	import {getData} from '@/controller/funcCommon'
	export default {
		data() {
			return {
				store: {
					kefu_status: 0,
					data: {
						kefu: {
							busy_reply: ''
						}
					}
				},
				clerk: {
					kefu_status: 1,
					kefu_status_cn: ''
				},
				showPreLoading: true
			}
		},
		methods: {
			onLoad() {
				var that = this;
				getData({
					vue: that,
					url: 'manage/kefu/setting/index',
					data: {},
					success: (res) => {
						that.store = that.util.extend(that.store, res.store);
						that.clerk = that.util.extend(that.clerk, res.clerk);
					}
				})
			},
			jsToggleSwitch(data) {
				var that = this;
				var confirm = null;
				if(data.value == 0) {
					confirm = '<div class="text-center">顾客消息开启有助于<br>提高用户下单转化率<br>如当前有会话未完成，建议您处理后再关闭<br>关闭后不再接收消息，但历史记录仍然保存</div>';
				}
				this.util.jsToggleSwitch({
					vue: that,
					key: data.keys,
					value: data.value,
					url: 'manage/kefu/setting/kefu_status',
					confirm: confirm,
					confirmButtonText: '关闭',
					data: {
						type: data.type,
						value: data.value
					}
				});
			},
		},
		mounted() {
			var that = this;
			that.onLoad();
		},
		components: {
			PublicHeader,
			iswitch
		},
	}
</script>

<style>
	#kefu-setting .status{
		border: 1px solid #fff;
	}
	#kefu-setting .status .iswitch{
		height: 24px;
	}
	#kefu-setting .status .tips{
		margin: 0 15px 9px 15px;
		padding: 10px;
		line-height: 1.4;
		font-size: 12px;
		background-color: rgba(51, 170, 252, 0.2);
		color: #33aafc;
	}
</style>


